"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import {
  COUNTRIES,
  CITIES,
  SKILLS,
  COUNTRY_CODES,
  MIN_SUMMARY_CHARS,
} from "@/lib/constants";

/* =======================
   Types
======================= */

interface SocialLinks {
  linkedin: string;
  whatsapp: string;
  meta: string;
  instagram: string;
}

interface UserProfile {
  id: string;
  professional_summary: string;
  phone: string;
  phone_country_code: string;
  location: string;
  skills: string[];
  preferred_countries: string[];
  social_links: SocialLinks | null;
}

interface EditProfileModalProps {
  isOpen: boolean;
  onClose: () => void;
  profile: UserProfile;
  onSuccess: () => void;
}

/* =======================
   Component
======================= */

export default function EditProfileModal({
  isOpen,
  onClose,
  profile,
  onSuccess,
}: EditProfileModalProps) {
  const [loading, setLoading] = useState(false);

  const [formData, setFormData] = useState({
    professional_summary: "",
    phone: "",
    phone_country_code: "+44",
    location: "",
    skills: [] as string[],
    preferred_countries: [] as string[],
    social_links: {
      linkedin: "",
      whatsapp: "",
      meta: "",
      instagram: "",
    } as SocialLinks,
  });

  const [newSkill, setNewSkill] = useState("");
  const [newCountry, setNewCountry] = useState("");

  const [filteredSkills, setFilteredSkills] = useState<string[]>([]);
  const [filteredCountries, setFilteredCountries] = useState<string[]>([]);
  const [filteredLocations, setFilteredLocations] = useState<string[]>([]);

  const [showSkillDropdown, setShowSkillDropdown] = useState(false);
  const [showCountryDropdown, setShowCountryDropdown] = useState(false);
  const [showLocationDropdown, setShowLocationDropdown] = useState(false);

  const [errors, setErrors] = useState<Record<string, string>>({});

  /* =======================
     Initialize Form
  ======================= */

  useEffect(() => {
    if (!profile) return;

    setFormData({
      professional_summary: profile.professional_summary || "",
      phone: profile.phone || "",
      phone_country_code: profile.phone_country_code || "+44",
      location: profile.location || "",
      skills: profile.skills || [],
      preferred_countries: profile.preferred_countries || [],
      social_links: {
        linkedin: profile.social_links?.linkedin || "",
        whatsapp: profile.social_links?.whatsapp || "",
        meta: profile.social_links?.meta || "",
        instagram: profile.social_links?.instagram || "",
      },
    });
  }, [profile]);

  /* =======================
     Validation
  ======================= */

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (formData.professional_summary.trim().length < MIN_SUMMARY_CHARS) {
      newErrors.professional_summary = `Professional summary must be at least ${MIN_SUMMARY_CHARS} characters`;
    }

    const phoneDigits = formData.phone.replace(/\s/g, "");
    if (phoneDigits && !/^\d{7,15}$/.test(phoneDigits)) {
      newErrors.phone = "Please enter a valid phone number";
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  /* =======================
     Submit
  ======================= */

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setLoading(true);

    try {
      const { error } = await supabase
        .from("profiles")
        .update({
          professional_summary: formData.professional_summary,
          phone: formData.phone,
          phone_country_code: formData.phone_country_code,
          location: formData.location,
          skills: formData.skills,
          preferred_countries: formData.preferred_countries,
          social_links: formData.social_links,
        })
        .eq("id", profile.id);

      if (error) throw error;

      onSuccess();
      onClose();
    } catch (err: any) {
      alert(err?.message || "Failed to update profile");
    } finally {
      setLoading(false);
    }
  };

  /* =======================
     Skills
  ======================= */

  const handleSkillInput = (value: string) => {
    setNewSkill(value);
    if (!value.trim()) return setShowSkillDropdown(false);

    setFilteredSkills(
      SKILLS.filter((s) =>
        s.toLowerCase().includes(value.toLowerCase())
      ).slice(0, 10)
    );
    setShowSkillDropdown(true);
  };

  const addSkill = (skill: string) => {
    const normalized = SKILLS.find(
      (s) => s.toLowerCase() === skill.toLowerCase()
    );
    if (!normalized || formData.skills.includes(normalized)) return;

    setFormData((prev) => ({
      ...prev,
      skills: [...prev.skills, normalized],
    }));
    setNewSkill("");
    setShowSkillDropdown(false);
  };

  const removeSkill = (index: number) =>
    setFormData((p) => ({
      ...p,
      skills: p.skills.filter((_, i) => i !== index),
    }));

  /* =======================
     Countries
  ======================= */

  const handleCountryInput = (value: string) => {
    setNewCountry(value);
    if (!value.trim()) return setShowCountryDropdown(false);

    setFilteredCountries(
      COUNTRIES.filter((c) =>
        c.toLowerCase().includes(value.toLowerCase())
      ).slice(0, 10)
    );
    setShowCountryDropdown(true);
  };

  const addCountry = (country: string) => {
    if (!COUNTRIES.includes(country)) return;

    setFormData((p) => ({
      ...p,
      preferred_countries: [...new Set([...p.preferred_countries, country])],
    }));
    setNewCountry("");
    setShowCountryDropdown(false);
  };

  const removeCountry = (index: number) =>
    setFormData((p) => ({
      ...p,
      preferred_countries: p.preferred_countries.filter((_, i) => i !== index),
    }));

  /* =======================
     Location
  ======================= */

  const handleLocationInput = (value: string) => {
    setFormData((p) => ({ ...p, location: value }));
    if (!value.trim()) return setShowLocationDropdown(false);

    setFilteredLocations(
      CITIES.filter((c) =>
        c.toLowerCase().includes(value.toLowerCase())
      ).slice(0, 10)
    );
    setShowLocationDropdown(true);
  };

  const selectLocation = (location: string) => {
    setFormData((p) => ({ ...p, location }));
    setShowLocationDropdown(false);
  };

  if (!isOpen) return null;

  /* =======================
     JSX
  ======================= */

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4">
      <div className="w-full max-w-2xl max-h-[90vh] overflow-y-auto rounded-xl bg-white">
        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          <button type="submit" disabled={loading}>
            {loading ? "Saving..." : "Save Changes"}
          </button>
        </form>
      </div>
    </div>
  );
}
